#root {
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-align-items: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

h1 {
  text-align: center;
}

td {
  height: 100px;
  width: 100px;
  background-color: #666;
  color: #fff;
  text-align: center;
  font-size: 36px;
}

td span {
  display: none;
  color: gray;
}

td:hover span {
  display: block;
}

.reset-game {
  text-align: center;
  margin: 20px auto;
}

.btn-lg {
  width: 100%;
  padding: 10px 10px;
  font-size: 36px;
}


